import { _decorator } from 'cc';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
export class FishInfo {
    public fishType:number;
    public blood:number;
    public wikiScale:number;
    constructor(fishType:number, blood:number, wikiScale:number){
        this.fishType = fishType;
        this.blood = blood;
        this.wikiScale = wikiScale;
    }
    public get name():string
    {
        return LanguageData.getLangByID("fish_shark_type_" + this.fishType);
    }
}

