import { _decorator, Component, Prefab, NodePool, Event, Node, Vec3, Vec2, EventTouch, UITransform, instantiate, sys, error} from 'cc';
const { ccclass, property } = _decorator;

import FishBulletBase from "../../../fish/script/FishBulletBase";
import MathUtils from "../../engine/utils/MathUtils";
import CannonManager from "./CannonManager";
import { MoveHelper } from "../../engine/utils/MoveHelper";
import FishNetManager from "./FishNetManager";
import GameMusicHelper from "../utils/GameMusicHelper";
import FishUI from "../../../fish/script/FishUI";
import CommonTips from "../../engine/uicomponent/CommonTips";
import { MsgFire } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFire';
import { gameNet } from 'db://assets/module_basic/scripts/NetGameServer';
import { MsgHitFish } from 'db://assets/module_basic/shared/protocols/public/fish/MsgHitFish';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';
import { FishSharkGameMgr } from 'db://assets/module_fish_shark/FishSharkGameMgr';
import { Logger } from 'db://assets/core_tgx/base/log/Logger';
import { MsgFireResult } from 'db://assets/module_basic/shared/protocols/public/fish/MsgFireResult';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';

export class waitBackBullet
{
    bulletID:number;
    bulletLevel:number;
    startPos:Vec3;
    endPos:Vec3;
    constructor(bulletID: number, bulletLevel: number,startPos:Vec3,endPos:Vec3) {
        // 将传入的参数赋值给类的属性
        this.bulletID = bulletID;
        this.bulletLevel = bulletLevel;
        this.startPos = startPos;
        this.endPos = endPos;
    }
}

@ccclass('BulletManager')
export default class BulletManager extends Component {
    public static instance: BulletManager = null;
    @property({ type: [Prefab] })
    private bulletPrefabList: Prefab[] = [];
    private bulletPool: Array<NodePool> = [];
    private bulletList: Array<FishBulletBase> = [];
    private bulletMoveSpeed: number = 30;
    private bulletMoveSpeed1: number = 800;
    private _vec3Cache;
    private _vec2Cache;
    private _fireTime: number = 0;
    private _fireTimeNew: number;
    private _bulletID:number = 0;
    private _waitBackBulletMap:Map<number,waitBackBullet> = new Map();
    onLoad() {
        this._vec3Cache = new Vec3();
        this._vec2Cache = new Vec2();
        BulletManager.instance = this;
        this.node.on(Node.EventType.TOUCH_START, this.onShootBullet, this)
    }

    start() {
        if(SubGameMgr.gameMgr)
        {
            var initData:any[] = (SubGameMgr.gameMgr as FishSharkGameMgr).getInitData();
            var lastBulletId:number = initData[3];
            this._bulletID = lastBulletId;  
        }
        gameNet.listenMsg("fish/FireResult", this.onNet_FireResult, this);
    }

    update(deltaTime: number) {

        this.checkMoveBulletNew(deltaTime);
    }
    private checkMoveBulletNew(deltaTime: number)
    {
        for (let i = this.bulletList.length - 1; i >= 0; i--) {
            let bullet: FishBulletBase = this.bulletList[i];
            let isMoving: boolean = MoveHelper.moveNodeNew(bullet.node, this.bulletMoveSpeed1,deltaTime)
            if (!isMoving) {
                this.bulletList.splice(i, 1);
                this.destroyBullet(bullet);
            }
        }
    }
    private onShootBullet(event: EventTouch) {

        var needCoin:number = FishSharkGameMgr.inst.getBulletCoin(CannonManager.instance.cannonType);
        if(needCoin == -1)
        {
            Logger.trace("等待配置返回");
            return;
        }
        //TOUCH_START 在Editor上，连续触发2次，导致发2次炮弹bug
        // if(sys.platform == "EDITOR_PAGE"){
        this._fireTimeNew = new Date().getTime();
        if(this._fireTimeNew - this._fireTime < 100){
            return;
        }
        this._fireTime = this._fireTimeNew;
        // }
        
        let tran = this.node.getComponent(UITransform);
        let location = event.getUILocation();
        this._vec3Cache.x = location.x;
        this._vec3Cache.y = location.y;
        this._vec3Cache.z = 0;
        tran.convertToNodeSpaceAR(this._vec3Cache, this._vec3Cache);
        let localP: Vec2 = new Vec2(this._vec3Cache.x, this._vec3Cache.y);
        FishUI.instance.playClickEffect(localP)

        //判断金钱是否足够

        if (FishUI.instance.score >= needCoin) {
            let bulletID:number = this.generateBulletID();
            this.sendFire(CannonManager.instance.cannonType,bulletID);
            this._vec3Cache = CannonManager.instance.getCannonPosition();
            this._waitBackBulletMap.set(bulletID,new waitBackBullet(bulletID,CannonManager.instance.cannonType,new Vec3(this._vec3Cache.x,this._vec3Cache.y,0),new Vec3(localP.x,localP.y,0)));
            //旋转炮台
            CannonManager.instance.rotateCannon(location);
        } else {
            CommonTips.showMsg(LanguageData.getLangByID("fish_shark_1"));
        }
    }
    private createBullet(data:waitBackBullet)
    {
        let rad: number = MathUtils.p2pRad(new Vec2(this._vec3Cache.x, this._vec3Cache.y), new Vec2(data.endPos.x,data.endPos.y))
        let rot: number = MathUtils.radiansToDegrees(rad)
        let bullet: FishBulletBase = this.createBulletNode(CannonManager.instance.cannonType,data.bulletID)
        bullet.targetP = new Vec2(data.endPos.x,data.endPos.y);
        this.node.addChild(bullet.node);
        bullet.node.setPosition(CannonManager.instance.getCannonPosition());
        this._vec3Cache.x = 1;
        this._vec3Cache.y = 1;
        this._vec3Cache.y = 1;
        Vec3.multiplyScalar(this._vec3Cache, this._vec3Cache, 2);
        bullet.node.setScale(this._vec3Cache)
        bullet.node.angle = rot;
        bullet.moveAngle = rot;
        this.bulletList.push(bullet)
        GameMusicHelper.playFire();
    }
    private createBulletNode(bulletType: number,bulletID:number) {
        let bulletNode: Node;
        if (this.bulletPool[bulletType] && this.bulletPool[bulletType].size() > 0) {
            bulletNode = this.bulletPool[bulletType].get();
        } else {
            bulletNode = instantiate(this.bulletPrefabList[bulletType-1])
        }
        bulletNode.getComponent(FishBulletBase).bulletType = bulletType;
        bulletNode.getComponent(FishBulletBase).bulletId = bulletID;
        return bulletNode.getComponent(FishBulletBase)
    }

    public killBullet(bullet: FishBulletBase) {
        let index: number = this.bulletList.indexOf(bullet);
        if (index >= 0) {
            this.bulletList.splice(index, 1);
            this.destroyBullet(bullet)
        }
    }
    //生成子弹ID
    private generateBulletID():number
    {
        this._bulletID++;
        return this._bulletID;
    }
    /**发射子弹 */
    private sendFire(bulletLevel:number,bulletId:number):void
    {
        var msg:MsgFire = {} as MsgFire;
        msg.bulletLevel = bulletLevel;
        msg.bulletId = bulletId.toString();

        gameNet.sendMsg('fish/Fire', msg);
    }
     /**打中鱼 */
    public sendHitFish(fishId:number,bulletId:number,bulletLevel:number):void
    {
        var msg:MsgHitFish = {} as MsgHitFish;
        msg.fishId = fishId.toString();
        msg.bulletId = bulletId.toString();
        msg.bulletLevel = bulletLevel;
        gameNet.sendMsg('fish/HitFish', msg);
    }
    private destroyBullet(bullet: FishBulletBase) {
       //临时代码，因为回收在内存卡顿。后面在优化 2023-2-10
       if(sys.platform == "EDITOR_PAGE"){
            bullet.node.destroy();
            return;
       }
 
        if (!this.bulletPool[bullet.bulletType]) {
            this.bulletPool[bullet.bulletType] = new NodePool();
        }
        this.bulletPool[bullet.bulletType].put(bullet.node)
    }

    private onNet_FireResult(msg:MsgFireResult):void
    {
        if(msg.isSuccess)
        {
            console.log("FireResult bulletId:"+msg.bulletId);
            FishUI.instance.score = Number(msg.coins);
            FishUI.instance.refreshScore();
            if(this._waitBackBulletMap.has(Number(msg.bulletId)))
            {
                this.createBullet(this._waitBackBulletMap.get(Number(msg.bulletId)));
            }
            else
            {
                Logger.error("FireResult not found:"+msg.bulletId);
            }
        }
        else
        {
            Logger.error("FireResult:"+msg.reason);
        }
        this._waitBackBulletMap.delete(Number(msg.bulletId));
    }

    onDestroy() {
        BulletManager.instance = null;
        gameNet.unlistenMsgAllByThis(this);
    }
}

