import { error, game, _decorator } from 'cc';
import DarkLayer from "../../engine/uicomponent/DarkLayer";
import LoadingPrefab from "../../engine/uicomponent/LoadingPrefab";
import MusicPrefab from "../../engine/uicomponent/MusicPrefab";
import SoundPrefab from "../../engine/uicomponent/SoundPrefab";
import { Logger } from "../../engine/utils/Logger";
import ResourcePrefab from "../prefab/ResourcePrefab";
import ShaderMaterialPrefab from "../prefab/ShaderMaterialPrefab";
import { ResourceMgr } from 'db://assets/core_tgx/base/ResourceMgr';
import { assetManager } from 'cc';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { SubGameMgr } from 'db://assets/module_basic/scripts/SubGameMgr';

export default class ResourcePreload {
    public static instance: ResourcePreload = new ResourcePreload();
    private isPreloaded: boolean = false;
    private totalNum: number = 7;
    private nowIndex: number = 0;
    private _porcessFun:Function;
    public async preLoad(callback: Function,porcessFun:Function) {
        this.nowIndex = 0;
        // if (this.isPreloaded) {
        //     callback();
        //     return;
        // }
        this.isPreloaded = true;
        this._porcessFun = porcessFun;
        if(porcessFun) porcessFun(this.nowIndex/this.totalNum);
        await LoadingPrefab.preLoad(); //1
        this.finishOneItemLoad();
        await DarkLayer.preLoad();  //2
        this.finishOneItemLoad();
        await MusicPrefab.preLoad();//3
        this.finishOneItemLoad();
        await SoundPrefab.preLoad();//4
        this.finishOneItemLoad();
        await ResourcePrefab.preLoad();//5
        this.finishOneItemLoad(); 
        await ShaderMaterialPrefab.preLoad();//6
        this.finishOneItemLoad(); //
        await this.preLoadGameScene();
        this.finishOneItemLoad();
        callback();
    }

    private finishOneItemLoad() {
        this.nowIndex++;
        if(this._porcessFun){
            this._porcessFun(this.nowIndex/this.totalNum);
        }
    }

    private async preLoadGameScene() {
        return new Promise((resolve, reject) => {
            let bundle = assetManager.getBundle(ModuleDef.FISH_SHARK);
            if(bundle)
            {
                bundle.preloadScene(SubGameMgr.gameMgr.gameScene.name,null,function ()
                {
                    resolve(true);
                }.bind(this));  //7
                
            }
            else
            {
                resolve(false);
            }
        });
        
    }

    public clearRes()
    {
        this.isPreloaded = false;
        LoadingPrefab.clear();
        MusicPrefab.destory();
        SoundPrefab.destory();
        ResourcePrefab.clear();
    }

    public restartGame(){
       
        this.clearRes();
        game.restart();
    }
}

